import React from "react";
import PropTypes from "prop-types";

import OraSwitcher from "../../../../core/components/ui/OraSwitcher";
import {integer, fraction} from "../../../../magnum2/components/Utils";
import MagnumPropositionListPriceTag from "./MagnumPropositionListPriceTag";

const MagnumOfferPropositionListItem = (props) => {
    const payNowPrice = formatPrice(props.offer.payNowPrice.value.toString(), props.offer.currency);

    return (
        <OraSwitcher className="u-padding-left-m u-padding-right-m u-padding-top-s" isSelected={props.selected} onChangeHandler={props.onChange}>
            <div className="l-row u-no-margin">
                <div className="o-radio opl-radio u-w-100 u-box-sizing">
                    <input type="radio" checked={props.selected} className="switcher-trigger"/><span className="o-ci"/>

                    <div >
                        <div className="l-row u-display_table u-small-block u-no-margin u-large-block">
                            <div className="l-col l-col-small-12 l-col-medium-6 l-col-12  u-display_table-cell u-small-block u-small-no-padding-r u-large-block u-large-no-padding-r">
                                <label className="o-ci-label u-font-normal g-black1-c u-no-padding" ><p dangerouslySetInnerHTML={{__html: props.offerDescription}}/></label>
                            </div>
                        </div>
                    </div>
                </div>
                <div className="l-col l-col-small-12 l-col-medium-3 l-col-12  u-clearfix u-display_table-cell u-small-block u-small-padding-top u-small-no-padding-r u-large-block u-large-padding-top u-large-no-padding-r u-medium-hide">
                    <div className="l-row u-large-margin-s u-small-margin-s">
                        <div className="l-col l-col-6  u-medium-hide u-no-padding-left">
                            <p>{props.descriptions.monthlyPayments}</p>
                        </div>
                        <div className="l-col l-col-small-6 l-col-medium-12 l-col-6  u-text-right">
                            <MagnumPropositionListPriceTag
                                offer={props.offer}
                                clientContext={props.clientContext}
                                loyaltyLength={props.loyaltyLength}
                                monthString={"mies."}
                                mainPriceClass={""}
                                alignClass={"u-left"}
                                offerDiscountInfoDescription={props.descriptions.offerDiscountInfoDescription}
                                offerDiscountInfoConvDescription={props.descriptions.offerDiscountInfoConvDescription}
                                baseId={"desktop"}
                            />
                        </div>
                    </div>
                </div>
                <div className="l-col l-col-small-12 l-col-medium-3 l-col-12  u-clearfix u-display_table-cell u-small-block u-small-no-padding-r u-large-block u-large-no-padding-r u-no-padding-left">
                    <div className="l-row u-large-margin-s u-small-margin-s">
                        <div className="l-col l-col-8 ">
                            <p className="u-medium-hide u-inline-block">{props.descriptions.activation}</p>
                        </div>
                        <div className="l-col l-col-small-6 l-col-medium-12 l-col-4 ">
                            <p className="u-font-bold u-small-inline-block u-text-right u-small-right u-large-right u-large-inline-block">{payNowPrice}</p>
                        </div>
                    </div>
                </div>
            </div>
        </OraSwitcher>
    );
};

MagnumOfferPropositionListItem.propTypes = {
    descriptions: PropTypes.shape({
        detailsLabel: PropTypes.string,
        monthlyPayments: PropTypes.string,
        activation: PropTypes.string
    }),
    offer: PropTypes.shape({
        currency: PropTypes.string,
        payNowPrice: PropTypes.shape({
            value: PropTypes.any
        }),
        monthlyPayments: PropTypes.arrayOf(PropTypes.shape({
            finalPrice: PropTypes.any
        }))
    }),
    selected: PropTypes.bool,
    offerDescription: PropTypes.string,
    onChange: PropTypes.func,
    clientContext: PropTypes.bool
};

export function formatPrice(price, currency) {
    return '' + integer(price) + ',' + fraction(price) + ' ' + currency;
}

export default MagnumOfferPropositionListItem;